import { RouterModule, Routes } from '@angular/router';
import { NgModule } from '@angular/core';
import { TeamLeadsComponent } from '@app/ffufm-feedback-dashboard-features/team-leads/team-leads.component';

const teamLeadsRoutes: Routes = [
  {
    path: '',
    component: TeamLeadsComponent
  }
];

@NgModule({
  declarations: [],
  imports: [
    RouterModule.forChild(teamLeadsRoutes)
  ],
  exports: [RouterModule]
})

export class TeamLeadsRouting {}


